.password-strength-container {
  padding: 20px;
  margin: 0 auto;
  max-width: 800px;
  font-family: Arial, sans-serif;
}

.password-input-container {
  gap: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.show-info-btn {
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  background-color: #4caf50;
}

.password-strength-message {
  color: #555;
  margin-top: 30px;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: gradientAnimation 15s ease infinite;
  background: linear-gradient(45deg, #d3cce3, #e9eff1, #a8c0ff);
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  25% {
    background-position: 100% 50%;
  }
  50% {
    background-position: 100% 100%;
  }
  75% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.wrapper-button {
  gap: 10px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .wrapper-button {
    align-items: center;
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .wrapper-button {
    gap: 15px;
    align-items: center;
    flex-direction: column;
  }
}
